.button-content {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  margin-top: 10%;
  margin-right: 5rem;
  float: right;
  padding-bottom: 5rem;
}

.home-icon {
  cursor: pointer;
  height: 300px;
  transition: all 0.3s ease 0s;
}
.btn {
  position: relative;
  display: flex;
}

.btn-icon-home {
  position: relative;
  min-height: 250px;
  height: 465px;
  width: 465px;
  right: 15px;
  bottom: 9px;
  cursor: pointer;
}
/* 3D BUTTON */

