@media (min-width: 360px) and (max-width: 768px) {
  .home {
    min-height: 800px;
    background: linear-gradient(to bottom, white 0%, #aad0d2 100%);
    /* background-color: #333333; */
    /* background: url(../../img/background-home-11.svg); */
    /* background-size: cover !important;
    -webkit-background-size: cover !important; */

    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: center;
  }

  .btn-icon-home {
    height: 250px;
    width: 250px;
    right: 20px;
    bottom: 20px;
  }
  .button-content {
    margin-top: 5%;
    padding-bottom: 25%;
  }
  .home .text-zone {
    width: 100%;
    height: 40%;
  }
  .button-content {
    margin-top: 1rem;
    margin-right: 4rem;
  }
}

