.btn {
  top: 0;
  left: 0;
  -webkit-transition: all 0.15s linear 0s;
  transition: all 0.15s linear 0s;
  position: relative;
  display: inline-block;
  margin-bottom: 3rem;
  width: 460px;
  height: 460px;
  background-color: #404040;
  color: #404040;
  font-family: arial;
  letter-spacing: 1px;
  border-radius: 300px;
  box-shadow: -6px 6px 0 #404040;
  text-decoration: none; }
  .btn:hover {
    top: 3px;
    left: -3px;
    box-shadow: -3px 3px 0 #404040; }
    .btn:hover::after {
      top: 1px;
      left: -2px;
      width: 4px;
      height: 4px; }
    .btn:hover::before {
      bottom: -2px;
      right: 1px;
      width: 4px;
      height: 4px; }
  .btn::after {
    -webkit-transition: all 0.15s linear 0s;
    transition: all 0.15s linear 0s;
    content: "";
    position: absolute;
    top: 2px;
    left: -4px;
    width: 8px;
    height: 8px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    z-index: -1; }
  .btn::before {
    -webkit-transition: all 0.15s linear 0s;
    transition: all 0.15s linear 0s;
    content: "";
    position: absolute;
    bottom: -4px;
    right: 2px;
    width: 8px;
    height: 8px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    z-index: -1; }

.btn p {
  font-size: 35px;
  font-weight: 800;
  font-family: "Courier New", Courier, monospace;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  position: absolute;
  margin-top: 6rem;
  margin-bottom: 0;
  color: #333333; }

a.btn {
  text-align: center;
  position: relative;
  color: #3333; }

a:active.btn {
  top: 6px;
  left: -6px;
  box-shadow: none; }
  a:active.btn:before {
    bottom: 1px;
    right: 1px; }
  a:active.btn:after {
    top: 1px;
    left: 1px; }

